import React, {useEffect, useState} from 'react'
import './utils/MakePage.css'
import { dbService } from '../tools/fbase';
import {Link} from 'react-router-dom';

import section0 from '../tools/img/section0.png';
import section1 from '../tools/img/section1.png';
import section3 from '../tools/img/section3.png';

const MakePage = ({history}) => {
    const [s1title, setS1title] = useState(" 랜딩페이지 제작부터\n데이터 분석까지 한번에");
    const [s1desc, setS1desc] = useState("스타트업 아이템 검증부터 신규 서비스, 이벤트까지 최고의 고객 전환율을 경험해보세요.");
    const [s1applyButton, setS1applyButton] = useState("9,900원에 시작하기");
    const [s2title, setS2title] = useState("코딩 필요없이 One-stop 랜딩페이지 제작 툴");
    const [s2desc, setS2desc] = useState("더 이상 개발자 눈치 보지 말고, 간편하게 랜딩 페이지를 만들어보세요.");
    const [s3title, setS3title] = useState("");
    const [s3desc, setS3desc] = useState("더 이상 개발자 눈치 보지 말고, 간편하게 랜딩 페이지를 만들어보세요.");
    // 텍스트들
    const [attachment1, setAttachment1] = useState(`${section0}`);
    const [attachment2, setAttachment2] = useState(`${section1}`)
    const [attachment3, setAttachment3] = useState(`${section3}`)
    // 사진들 아래는 기타
    const [mainColor, setMainColor] = useState("#6B63F7");
    const [sectionOneImageLeft, setSectionOneImageLeft] = useState(0);
    const [secNum, setSecNum] = useState(0);
    const [font, setFont] = useState("Noto Sans KR");
    const [loading, setLoading] = useState(false);
    const secNums = [0,1,2,3];

    const onSubmit = async () => {
        setLoading(true);

        console.log("제출완료!")

        // 사진을 먼저 업로드하고 그 URL을 받아서 데이터로 넣어줘야한다.
        // const attachmentRef = stService.ref().child(`${userObj.uid}/${uuidv4()}`)
        // const response = await attachmentRef.putString(attachment, "data_url");
        // const attachmentURL = await response.ref.getDownloadURL();

        setLoading(false);
        history.push('/#/submit');
        history.go();
    }

    const onFileChange =  (e, num) => {
        console.log("e", num);
        const {target:{files},} = e;
        const oneFile = files[0];
        const reader = new FileReader();
        reader.onloadend = (finishedEvent) => { // 로딩이 끝날 때 실행한다는 뜻.
            const {currentTarget:{result}} = finishedEvent;
            switch (num){
                case 1:
                    setAttachment1(result);
                    break;
                case 2:
                    setAttachment2(result);
                    break;
                case 3:
                    setAttachment3(result);
                    break;

            }
        }
        reader.readAsDataURL(oneFile);
    }

    const selectorList = secNums.map((item,index) => {
        if(item === secNum){
            var borderBottom = '';
        }else{
            var borderBottom = '1px solid black';
        }
        if(item === 0){
            return (
                <span index={index} style={{borderBottom:`${borderBottom}`}} className="make-page-section-selector" onClick={(e) => {
                    setSecNum(item);
                    }}>
                    기본세팅
                </span>
            )
        }else{
        return(
            <span index={index} style={{borderBottom:`${borderBottom}`}} className="make-page-section-selector" onClick={(e) => setSecNum(item)}>
                {item}번
            </span>
            )
        }
    })

    const selectorTable = () => {
        switch (secNum){
            case 0:
                return <>
                    <div className="section-one-make">
                        <div className="section-one-make-one">
                            <div>
                                - 색상
                            </div>
                            <div style={{display:'flex', flexDirection:'row', justifyContent:'center'}}>
                            <span className="colorSelection">
                                1번
                                <input value={mainColor} type="color" onChange={e => setMainColor(e.currentTarget.value)}/>
                            </span>
                            <span className="colorSelection">
                                2번
                            </span>
                            <span className="colorSelection">
                                3번
                            </span>
                            </div>
                        </div>
                        <div className="section-one-make-one">                        
                            <div>
                                - 폰트
                            </div>
                            <div style={{display:'flex', flexDirection:'column', justifyContent:'center'}}>
                                <span className="fontSelection" onClick={e => setFont("Noto Sans KR")} style={{fontFamily:"Noto Sans KR"}}>
                                    다람쥐 쳇바퀴 굴러가는 소리
                                </span>
                                <span className="fontSelection" onClick={e => setFont("ImcreSoojin")} style={{fontFamily:"ImcreSoojin"}}  >
                                    다람쥐 쳇바퀴 굴러가는 소리
                                </span>
                                <span className="fontSelection">
                                    다람쥐 쳇바퀴 굴러가는 소리
                                </span>
                            </div>
                        </div>
                    </div>
                </>
            case 1:
                return <>
                <div className="section-one-make">
                    1번 타이틀
                    <textarea value={s1title} type="text" onChange={e => setS1title(e.currentTarget.value)} className="section-one-title-input"/>
                    1번 설명
                    <input value={s1desc} type="text" onChange={e => setS1desc(e.currentTarget.value)} className="section-one-title-input"/>
                    1번에 버튼 텍스트
                    <input value={s1applyButton} type="text" onChange={e => setS1applyButton(e.currentTarget.value)} className="section-one-title-input"/>
                    <input type="file" accept="image/*" className="file-input" onChange={(e) => onFileChange(e, 1)}/>
                </div>
                </>
            case 2:
                return <>
                <div className="section-one-make">
                    2번 타이틀
                    <input value={s2title} type="text" onChange={e => setS2title(e.currentTarget.value)} className="section-one-title-input"/>
                    2번 설명
                    <input value={s2desc} type="text" onChange={e => setS2desc(e.currentTarget.value)} className="section-one-title-input"/>
                    2번 사진
                    <input type="file" accept="image/*" className="file-input" onChange={e => onFileChange(e,2)}/>
                </div>
                </>
            case 3:
                return <>
                <div className="section-one-make">
                    3번 타이틀
                    <input value={s3title} type="text" onChange={e => setS3title(e.currentTarget.value)} className="section-one-title-input"/>
                    3번 설명
                    <input value={s3desc} type="text" onChange={e => setS3desc(e.currentTarget.value)} className="section-one-title-input"/>
                    3번 사진
                    <input type="file" accept="image/*" className="file-input" onChange={e => onFileChange(e,3)}/>
                </div>
                </>
        }
    }

    return (
        <div className="make-page-container">
        {/* 여기서부터 메인페이지 베껴옴 */}
        <div className="main-page-container" style={{width:'70%'}}>
            <div className="main-page-section" style={{backgroundColor:`${mainColor}`}}>
                <div className="main-section-left" style={{color:'white'}}>
                    <span className="one-section-big" style={{fontFamily:`${font}`}}>
                        {s1title}
                    </span>
                    <span className="one-section-small">
                        {s1desc}
                    </span>
                    <span className="apply-button">
                        {s1applyButton}
                    </span>
                </div>
                <div className="main-section-right">              
                        {attachment1 && 
                        <div>
                            <img src={attachment1} className="col-image"/>
                        </div>
                        }
                </div>
            </div>
            <div className="main-page-section" style={{backgroundColor:"white", color:'#505050'}}>
                <div className="main-section-left">
                    <span className="one-section-big" style={{fontFamily:`${font}`}}>
                        {s2title}
                    </span>
                    <span className="one-section-small">
                        {s2desc}
                    </span>
                </div>
                <div className="main-section-right">               
                        {attachment2 && 
                        <div>
                            <img src={attachment2} className="col-image"/>
                        </div>
                        }
                </div>
            </div>
            <div className="main-page-section" style={{backgroundColor:"white", color:'#505050'}}>
                <div className="main-section-right">
                    This is Section2 Right
                </div>
                <div className="main-section-left">
                    <div className="middle-section">
                        <span className="one-section-big">
                            {s3title}
                        </span>
                        <span className="one-section-small">
                            {s3desc}
                        </span>
                    </div>  
                </div>
            </div>
            <div className="main-page-section" style={{backgroundColor:"white", color:'#505050'}}>
                <div className="main-section-left">
                    <div className="middle-section">
                        <span className="one-section-big">
                            제작부터 데이터 분석을<br/>9,990원의<br/>합리적인 가격으로
                        </span>
                        <span className="one-section-small">
                            평균 30만원의 랜딩페이지 제작비용을 <br/>
                            9,900원으로 줄였어요.
                        </span>
                    </div>
                </div>
                <div className="main-section-right">          
                        {attachment3 && 
                        <div>
                            <img src={attachment3} className="col-image"/>
                        </div>
                        }
                </div>
            </div>
            <div className="main-page-section-last" style={{backgroundColor:`${mainColor}`}}>
                <div className="main-section-last-title">
                    랜딩페이지 제작<br/>지금 시작하기
                </div>
                <Link to="makePage" className="apply-button">이동하기</Link>
            </div>
        </div>
        {/* 여기까지 메인페이지 베껴옴 */}
            <div className="make-page-make-space">
                작성
                <div className="section-selector-container">
                    {selectorList}
                </div>
                <div className="section-table-container">
                    {selectorTable()}
                </div>
                <button onClick={onSubmit} style={{width:'50%'}}>등록</button>
            </div>
            {/* 로딩창 */}
        </div>
    )
}

export default MakePage
